/// solution

function isSorted(array) {
  switch (array.length) {
    case 0: return true
    case 1: return true
    default:
      let current = array[0]
      let next = array[1]
      if (next < current) {
        return false
      }
      return isSorted(array.slice(1))
  }
}

/// tests

import { test } from 'ava'

test(t => t.is(isSorted([]), true))
test(t => t.is(isSorted([-9, -5, 0, 3, 9]), true))
test(t => t.is(isSorted([3, 9, -3, 10]), false))
